

<template>

<div class="card mt-4">
<h4>Other Users Location & Notes</h4>
<table class="table m-0">
      <thead>
        <tr>
          <th scope="col">Name</th>
          <th scope="col">Notes</th>
          <th scope="col">Location</th>
        </tr>
      </thead>
      <tbody>
            <tr v-for="{ id, name, description ,location} in users" :key="id">
          <td>{{ name }}</td>
          
            <td>{{ description }}</td>
            <td>{{ location }}</td>
    </tr>
      </tbody>
    </table>
</div>

</template>



<script>
import { useLoadUsers } from '@/firebase'
export default {

 
  setup() {
     const users = useLoadUsers()
    return {users};

  }
}
</script>