import firebase from "firebase/compat/app";
import "firebase/compat/firestore";
import 'firebase/compat/auth';

import { ref, onUnmounted } from 'vue'

const config = {
    apiKey: "AIzaSyDq8pon5-6gLvtVw2HOIU3ssIMZXtxgFpQ",
    authDomain: "my-community-group.firebaseapp.com",
    projectId: "my-community-group",
    storageBucket: "my-community-group.appspot.com",
    messagingSenderId: "1060003160178",
    appId: "1:1060003160178:web:ed4cd0c0f823332c4aa2eb",
    measurementId: "G-DHNGW374V8"
  };

const firebaseApp = firebase.initializeApp(config)
const db = firebaseApp.firestore()
const usersCollection = db.collection('Users')

export const createUser = user => {
  return usersCollection.add(user)
}

export const useLoadUsers = () => {
  const Users = ref([])
  const close = usersCollection.onSnapshot(snapshot => {
    Users.value = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }))
  })
  onUnmounted(close)
  return Users
}