<template>
 <div class="container">
        <div class="float-container justify-content-center">
            <div class="float-child">
                <div class="card">
                    <div class="card-header"><h6>My Current Location coordinates  </h6><p>{{center}} </p></div>
                        
                      
                         <GmapMap
                         :center='center'
                         :zoom='12'
                            style='width:100%;  height: 200px;'/>
                    </div>
            </div>
            <div class="float-child">
              <h6>Add Users Notes</h6>
              <form @submit.prevent="onSubmit">
                          <div class="form-group">
        <label>Name</label>
        <input v-model="form.name" class="form-control" required />
      </div>

      <div class="form-group mt-3">
        <label>Description</label>
        <input
          v-model="form.description"
          class="form-control"
          type="text"
          required
        />
      </div>
    <input
          v-model="form.location"
          class="form-control"
          type="hidden"
          
          required
        />
      <button type="submit" class="btn btn-success mt-3">
       Add Notes
      </button>
                        </form>

                        <!--------------------------------View Data---------------------------------->
  
                  
            </div>
          
</div>
  </div>
</template>

<script>
import { createUser } from '@/firebase'
import { reactive } from 'vue'
import { useLoadUsers } from '@/firebase'

export default {
 
  name: 'GoogleMap',
  data() {
    return {
      center: { lat: -26.835, lng: 152.963 }, 
       markers: [],
       places: [],
       lname: '',
       ldescription: '',
       u:'',
       j:'',
         arr: []    
          
    }
  },

  mounted() {
    
   this.geolocate();
    this.getStreetAdress();
  },

  methods: {
    
    geolocate: function() {
       
      navigator.geolocation.getCurrentPosition(position => {
        this.center = {
          lat: position.coords.latitude,
          lng: position.coords.longitude,
        };
      });
    },
  
    getStreetAdress() {

       console.log(this.center.lat);
        console.log(this.center.lng);
      var pos=this.center.lat + "," +this.center.lng;

        localStorage.setItem("position", pos);
            const users = useLoadUsers()
    return {users};
         

    },

    formSubmit(e) {
                e.preventDefault();
              
                
              
                },

    
  },
  setup() {
    
   var x=localStorage.getItem("position");

    const form = reactive({ name: '', description: '' , location:x})
    const onSubmit = async () => {
      await createUser({ ...form })
      form.name = ''
      form.description = ''
      form.location = ''
    }
    return { form, onSubmit }
  },
};
</script>